import React, { createContext, useState, useContext, useEffect } from "react";
import axios from "axios";
import {LIMIT_PER_PAGE} from '../utils/constants';

// Create the context
const LettersContext = createContext();

// Custom hook to use the LettersContext
export const useLetters = () => useContext(LettersContext);

// Provider component to wrap around components that need access to letters
export const LettersProvider = ({ children }) => {
  const [letters, setLetters] = useState([]);
  const [totalLetters, setTotalLetters] = useState(0);
  const [totalPages, setTotalPages] = useState(0);
  const [isFetching, setIsFetching] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);

  const fetchLetters = (page, limitPerPage) => {
    setIsFetching(true);
    axios
      .get(`${process.env.REACT_APP_API_URL}/letters?page=${page}&limit=${limitPerPage}`)
      .then((response) => {
        console.log("Letters Fetched");
        setLetters(response.data.letters);
        setTotalLetters(response.data.totalLetters);
        setTotalPages(response.data.totalPages);
        setCurrentPage(response.data.currentPage);
        setIsFetching(false);
      })
      .catch((error) => {
        console.error("Error fetching letters:", error);
        setIsFetching(false);
      });
  };
  
  // Function to add the new letter
  const addNewLetter = (newLetter) => {
    // Add the new letter to the front of the letters array
    if (letters.length + 1 > LIMIT_PER_PAGE) {
      setLetters([newLetter, ...letters.slice(0, letters.length - 1)]);
    } else {
      setLetters([newLetter, ...letters]);
    }
    setTotalLetters(totalLetters + 1);
    setTotalPages(Math.ceil((totalLetters + 1) / LIMIT_PER_PAGE))
  };

  // Run fetchLetters once when context loads
  useEffect(() => {
    fetchLetters(currentPage, LIMIT_PER_PAGE);
  }, []); // Empty dependency array to run on mount only


  return (
    <LettersContext.Provider
      value={{ letters, totalLetters, totalPages, isFetching, currentPage, setCurrentPage, fetchLetters, addNewLetter }}
    >
      {children}
    </LettersContext.Provider>
  );
};
